import React, { useState, useEffect, useContext } from 'react';
import CircuitGroup from './CircuitGroup';
import getKey from './KeyGenerator';
import CollapseToggleButton from './CollapseToggleButton';
import { CircuitContext } from '../context/CircuitContext';
import initCircuitTracking from './initCircuitTracking';
import initializeData from './initializeData';
import icons from './icons';

const TrackCircuit = ({
}) => {
    
    const { 
        circuits, 
        setCircuits, 
        sort, 
        setSort,
        setActivated,
        ticker,
        setTicker,
        countdown,
        setCountdown 
    } = useContext(CircuitContext);

    const [, forceUpdate] = useState();
    
    console.log(`TrackCircuits => circuits: ${JSON.stringify(circuits,null,2)}`)
    const notNull = (value) => (value !== null) ? true : false;
    const notEmpty = (value) => (value !== "") ? true : false;
    const isGood = (value) => (notNull(value) && notEmpty(value)) ? true : false;
    const localData = () => initializeData('circuitTracking', initCircuitTracking);

    useEffect(() => {
        console.log(`TrackCircuit => circuits update`);
        forceUpdate(Math.random());
    }, [circuits]);

    const addCircuit = (circuitGroupIndex, circuitIndex) => {
        //const updatedCircuits = [...circuits];
        const updatedCircuits = [...localData()];
        const circuit = prompt('Circuit name:', '');
        //const circuitDescription = prompt('Circuit description:', '');
        const description =  'Set the Timer: Determine the duration for each exercise, usually around 30 seconds to 1 minute. Arrange the exercises in a sequence that allows you to transition smoothly from one exercise to the next. Consider alternating between upper body and lower body exercises to give specific muscle groups time to rest.';
        
        const newCircuit = {
            title: circuit,
            description: description,
            //circuit: circuit
            //circuit: '',
            time: 5,
            restTime: 5,
            excersizes: [],
            isCollapsed: false
        }
        if (isGood(circuit)/* || isGood(circuit)*/) {
            updatedCircuits[circuitGroupIndex].circuits.push(newCircuit)
            setCircuits(updatedCircuits);
        }

    };
    const subtractCircuit = (circuitGroupIndex, circuitIndex) => {
        //const updatedCircuits = [...circuits];
        const updatedCircuits = [...localData()];
        const circuit = updatedCircuits[circuitGroupIndex].circuits[circuitIndex];
        circuit.sessions = circuit.sessions ?? [];
        circuit.isRunning = false;
        circuit.sessions.pop();
        setCircuits(updatedCircuits);
    };

    const deleteGroup = (circuitGroupIndex) => {
        //const toggle = window.confirm(`Are you sure you want to remove circuit group ${circuits[circuitGroupIndex].title}`)
        const toggle = window.confirm(`Are you sure you want to remove circuit group ${localData()[circuitGroupIndex].title}`)
        const removeItemByIndex = (array, index) => {
            if (index >= 0 && index < array.length) {
                array.splice(index, 1);
            } else {
                console.error("Index out of range");
            }
        };
        if (toggle) {
            //const updatedCircuits = [...circuits];
            const updatedCircuits = [...localData()];
            removeItemByIndex(updatedCircuits, circuitGroupIndex);
            setCircuits(updatedCircuits);
        }
    }

    return (
        <div key={getKey('circuitGroupContainer')}>
            <div className='containerBox grid200'>
                <div className='containerBox bg-lite centerVertical p-20 color-yellow button' onClick={()=>setTicker(!ticker)}>ticker: {(ticker)?icons.soundOn:icons.soundOff}</div>
                <div className='containerBox bg-lite centerVertical p-20 color-yellow button' onClick={()=>setCountdown(!countdown)}>countdown: {(countdown)?icons.alarmOn:icons.alarmOff}</div>
            </div>
            <div className='containerBox bg-lite centerVertical p-20 color-yellow bold size25'>
                <CollapseToggleButton
                    title={'sort'}
                    isCollapsed={sort}
                    setCollapse={setSort}
                    align='center'
                />
            </div>
            {
                (sort)
                //? circuits.map((circuitGroup, circuitGroupIndex, array) => (
                ? localData().map((circuitGroup, circuitGroupIndex, array) => (
                    <div key={getKey('circuitGroups')}>
                        <CircuitGroup
                            //circuits={circuits}
                            //setCircuits={setCircuits}
                            circuitGroup={circuitGroup}
                            circuitGroupIndex={(array.length - 1 - circuitGroupIndex)}
                            deleteGroup={deleteGroup}
                            addCircuit={addCircuit}
                            //setScroll={setScroll}
                            //targetElementRef={targetElementRef}
                            //scrollToBottom={scrollToBottom}
                            //sort={sort}
                        />
                    </div>
                ))
                //: circuits.map((circuitGroup, circuitGroupIndex) => (
                : localData().map((circuitGroup, circuitGroupIndex) => (
                    <div key={getKey('circuitGroups')}>
                        <CircuitGroup
                            //circuits={circuits}
                            //setCircuits={setCircuits}
                            circuitGroup={circuitGroup}
                            circuitGroupIndex={circuitGroupIndex}
                            deleteGroup={deleteGroup}
                            addCircuit={addCircuit}
                            //setScroll={setScroll}
                            //targetElementRef={targetElementRef}
                            //scrollToBottom={scrollToBottom}
                        />
                    </div>
                ))
            }
        </div>
    )
}

export default TrackCircuit