import React, { useContext } from 'react';
import icons from './icons';
import { CircuitContext } from '../context/CircuitContext';
import initCircuitTracking from '../utils/initCircuitTracking';
import initializeData from '../utils/initializeData';

const CircuitNavigation = ({
    circuit,
    circuitGroupIndex,
    circuitIndex,
    toggleEditCircuit,
    editCircuit
}) => {

    const {
        circuits,
        setCircuits,
        activeIndex,
        setActiveIndex,
        activated,
        setActivated,
        setExcersizeTime,
        setRestTime
    } = useContext(CircuitContext);

    const deleteCircuit = () => {
        const toggle = window.confirm(`Are you sure you want to remove circuit: ${circuit.title}`)
        const removeItemByIndex = (array, index) => {
            if (index >= 0 && index < array.length) {
                array.splice(index, 1);
            } else {
                console.error('Index out of range');
            }
        };
        if (toggle) {
            const localData = initializeData('circuitTracking', initCircuitTracking);
            //const newCircuits = [...circuits];
            const newCircuits = [...localData];
            removeItemByIndex(newCircuits[circuitGroupIndex].circuits, circuitIndex);
            setCircuits(newCircuits);
        }
    }

    return <div>
            <div className='flexContainer'>
            <div className='containerBox flex3Column p-20 button contentCenter'>
                    <div onClick={
                        () => setExcersizeTime(
                                    JSON.parse(localStorage.getItem('circuitTracking'))[circuitGroupIndex].circuits[circuitIndex].time,
                                    circuitGroupIndex,
                                    circuitIndex
                                )
                    }>
                        {icons.restart}
                    </div>
                </div>
                <div className='containerBox flex3Column p-20 button contentCenter'>
                    <div onClick={() => toggleEditCircuit()}>
                        {
                            (editCircuit)
                                ? <div className='color-neogreen bold'>save</div>
                                : icons.edit
                        }
                    </div>
                </div>
                <div
                    className='containerBox flex3Column p-20 button contentCenter'
                    onClick={() => deleteCircuit()}
                >
                    {icons.delete}
                </div>
            </div>
        </div>
}

export default CircuitNavigation;